@import url(https://fonts.googleapis.com/css?family=Lobster&display=swap);
@import url(https://fonts.googleapis.com/css?family=Bebas+Neue&display=swap);
@import url(https://fonts.googleapis.com/css?family=Montserrat&display=swap);
@import url(https://fonts.googleapis.com/css?family=Open+Sans+Condensed:300&display=swap);
body {
  height: 100%;
  width: 100%;
}

.app {
  height: 100%;
  width: 100%;
  background-color: #1f222a;
}

.social-panel {
  display: flex;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  justify-content: space-between;
  align-items: flex-end;
  padding: 1em 1em;
}
.social-badge {
  margin-left: 0.5em;
  margin-right: 0.5em;
  border: none;
  background: none;
  cursor: pointer;
}

.name {
  display: flex;
  padding: 0em 1em;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
}

.name-icon {
  color: #8e9092;
  font-size: 4em;
  font-weight: 700;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  font-family: 'Lobster', cursive;
}

.name-title {
  display: flex;
  color: #8e9092;
  align-items: center;
  margin-left: 0.5em;
  font-family: 'Bebas Neue', cursive;
  letter-spacing: 0.25em;
}

.header {
  display: flex;
  justify-content: space-between;
  width: 100%;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
}

.splash {
  display: flex;
  align-items: center;
  flex-direction: column;
  width: 100%;
  height: 100%;
}

.splash-hello-container {
  margin-top: 15em;
  margin-right: 25em;
}

.splash-hello {
  color: #ffffff;
  font-size: 8em;
  font-weight: 800;
  font-family: 'Montserrat', sans-serif;
}

.splash-secondary-container {
  display: flex;
  margin-top: -1em;
}

.splash-quote {
  color: #ffffff;
  font-family: 'Open Sans Condensed', sans-serif;
  letter-spacing: 0.10em;
}

.splash-hr {
  width: 4em;
  height: 0.005em;
  color: #ffffff;
}
html {
  height: 100vh;
  width: 100vw;
}

body {
  margin: 0;
  padding: 0;
  height: 100%;
  width: 100%;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}



.App {
  background-color: #1a276f;
  height: 100vh;
  width: 100vw;
}
#root {
  width: 100%;
  height: 100%;
}

.app {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.content-container {
  display: flex;
  justify-content: space-between
}

.social-container {
  display: flex;
  justify-content: space-between;
  padding: 2em;
  font-size: 3em;
  font-size: 2.5em;
  min-width: 10em;
  padding-top: 5em;
  color: #464444;
}

